body {
  background-image: url("./wallback.jpg");
  background-size: contain;
  background-repeat: repeat;
  background-position: center;
  font-family: 'Kaushan Script', cursive; }


.app {
  padding: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }


h1 {
  position: absolute;
  top: 0;
  left: 2%;
